import React from 'react';
import { Navigation } from '@shopify/polaris';
import { useNavigate } from 'react-router-dom';

interface NavigationProps {
  selected: string;
  onSelect: (selected: string) => void;
}

const NavigationComponent: React.FC<NavigationProps> = ({ selected, onSelect }) => {
  const navigate = useNavigate();

  const handleNavigation = (path: string, key: string) => {
    onSelect(key);
    navigate(path);
  };

  return (
    <Navigation location="/">
      <Navigation.Section
        items={[
          {
            label: 'AI Impact Score',
            selected: selected === 'home',
            onClick: () => handleNavigation('/', 'home'),
          },
          {
            label: 'Interview Assignments',
            selected: selected === 'assignments',
            onClick: () => handleNavigation('/interview-assignments', 'assignments'),
          },
          {
            label: 'Interview Analysis',
            selected: selected === 'analysis',
            onClick: () => handleNavigation('/interview-analysis', 'analysis'),
          },
        ]}
      />
    </Navigation>
  );
};

export default NavigationComponent; 